import PublicLeft from '../page/PublicLeft.vue'

export default {
	name: 'Index',
	data () {
		return {
			userInfo: {name: 'liying', phone: '15195601069', email: '2332893183@qq.com', github: 'https://coding.net/u/missYing/p/baseInfo/git'},
		    baseInfo: false,
		    workHistory: false,
		    other: false,
		    mongoose: false
		}
	},
	mounted(){
	    this.getUserInfo()
	},
	components: {
		PublicLeft
	},
	methods: {
		initActive: function(path) { // 初始化menu选中
			switch(path){
				case "/":
				case "/baseInfo":
					this.baseInfo = "active"
					break;
				case "/other":
					this.other = "active"
					break;
				case "/mongoose":
					this.mongoose = "active"
					break;
				default:
					this.workHistory = "active"
			}
		},
	    getUserInfo: function(e) {
	    	// 显示
			// this.$vux.toast.show({
			//  text: 'Loading'
			// })
			let path = this.$route.path
			this.initActive(path) // 初始化menu选中

	        // 获取用户信息
			this.$axios.get('/api/user/getUserInfo')
			.then((res) => {
				if(res.status == 200) { //发送成功
                    if(res.data.errors) { //参数错误
                        this.$vux.toast.show({text: res.data._message, type: 'cancel'})
                    }
                    else { //请求正确
                        if(res.data.length > 0) this.userInfo = res.data[0];
                    }
                }
			})
			.catch((reject) => {
				console.log(reject)
				this.$vux.toast.show({text: '获取用户信息请求失败！',type: 'cancel'})
			})
	    }
	},
	watch: {
		'$route' (to, from) { // 监听路由变化
			switch(from.path){
				case "/":
				case "/baseInfo":
					this.baseInfo = ""
					break;
				case "/other":
					this.other = ""
					break;
				case "/mongoose":
					this.mongoose = ""
					break;
				default:
					this.workHistory = ""
			}
			this.initActive(to.path)
		}
	}
}



